body {
    background-color: #e6e6e6;
}
.wrapper {
    font: 14px "Century Gothic", Futura, sans-serif;
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

li button {
    border-radius: 10px;
    padding: 10px;
}

.selected {
    background-color:blanchedalmond;
}

.log {
    display: flex;
    flex-direction: column;
    margin-left: 0;
}
.board {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.item {
    width: 40px;
    height: 40px;
    background-color: aliceblue;
    border: 1px solid #666;
    display: flex;
    align-items: center;
    justify-content: center;
}

.row {
    display: flex;

}

.log-btn {
    font-size: 20px;
    font-weight: 400;

}

button {
    font-size: 33px;
    font-weight: 700;
}

.win {
    background-color: aquamarine;
}